@font-face {
  font-family: digital;
  src: url(fonts/digital7.ttf);
}

.container {
  position: fixed;
  top: 85px;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: auto;
  background-color: rgb(53, 53, 53);
}

.drum-machine {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border: 4px solid rgb(255, 194, 103);
  padding: 20px;
  display: flex;
  justify-content: space-around;
  width: 40%;
  height: 300px;
  background-color: rgb(198, 210, 220);
}

.board {
  flex: 55%;
  margin-left: -20px;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.drum-pad {
  width: 25%;
  margin: 2px;
  border-radius: 10px;
  height: 30%;
  background-color: rgb(36, 36, 36);
  color: wheat;
  font-size: 1.3em;
}

.display {
  background-color: lavender;
  flex: 1;
  text-align: right;
  width: 90%;
  height: 50px;
  padding: 10px;
  font-size: 1.9em;
  font-family: digital;
  margin-top: 30px;
  margin-bottom: 50px;
}

.controls {
  flex: 45%;
  justify-content: space-around;
  margin-left: 10px;
}

